@import "C:/Users/juyou/nomadgram/frontend/src/config/_variables.scss";
.container {
  margin: 10px auto;

  .spinner {
    height: 32px;
    animation: spinner 1.2s steps(12) infinite;
  }
}

@keyframes spinner {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg)
  }
}



