@import "C:/Users/juyou/nomadgram/frontend/src/config/_variables.scss";
.horizontal,
.vertical {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 16px;
  width: 100%;
  min-height: 50px;
  box-sizing: border-box;
  border-bottom: 1px solid $light-grey;

  @include breakpoint("photn-tablet") {
    border-bottom: 0;
  }

  .column {
    display: flex;
    align-items: center;

    .user {
      margin-left: 10px;

      .username {
        font-weight: 600;
        margin-right: 5px;
        margin-bottom: 3px;
      }

      .name {
        color: $dark-grey;
      }
    }

    .bigAvatar {
      width: 55px;
      border-radius: 50%;
    }

    .avatar {
      width: 30px;
      border-radius: 50%;
    }

    .button {
      @include blueButton();
    }
  }

  .image-wrapper {
    width: 50px;
    height: auto;
    overflow: hidden;
    position: relative;

    .image {
      position: relative;
      padding-top: 100%;
      /* 1:1 ratio */
      overflow: hidden;

      .image-centered {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        -webkit-transform: translate(50%, 50%);
        -ms-transform: translate(50%, 50%);
        transform: translate(50%, 50%);

        img {
          position: absolute;
          top: 0;
          left: 0;
          -webkit-transform: translate(-50%, -50%);
          -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
        }
      }
    }
  }
}

.horizontal {
  &:last-child {
    border: 0;
  }
}

.vertical {
  flex-direction: column;
  width: 30%;
  justify-content: center;
  align-items: center;
  height: 220px;
  margin-bottom: 50px;
  margin-right: 30px;

  @include boxBorder(yes);

  .column {
    flex-direction: column;

    .avatar {
      width: 78px;
      margin-bottom: 20px;
    }

    .user {
      text-align: center;
      margin: 0;

      .username {
        margin-bottom: 7px;
      }
    }
  }

  .button {
    margin-top: 15px;
    min-width: 95px;
  }
}



